<template>
    <v-card>
        <v-card-title class="text-button font-weight-black">
            <v-progress-circular indeterminate color="primary" v-show="loading">
            </v-progress-circular>
            {{ shoukuandan_id }}
            <v-spacer></v-spacer>
            <template v-if="skd_info.status == 1">
                <div class="red--text">{{ skd_info.cancel_remark }}</div>
            </template>
            <template v-else-if="skd_info.status == 0">
                <v-form v-model="valid">
                    <v-text-field placeholder="" v-model="cancel_remark" dense hide-details="auto" color="error"
                        :rules="[v => !!v]"></v-text-field>
                </v-form>
                <v-btn color="error" class="ml-5" small :disabled="!valid" @click.once="btn_cancel">
                     
                </v-btn>
            </template>
        </v-card-title>
        <v-card-text>
            <div style="border:1px solid #eee;background:#FFEBEE">
                <!--  -->
                <div class="print pa-4">
                    <!-- header -->
                    <div class="text-center text-h6 font-weight-black">
                        {{ skd_info.xiaoqu.name }}</div>
                    <v-divider class="my-1"></v-divider>
                    <div class="d-flex justify-space-between text-button font-weight-black">
                        <span>{{ skd_info.loudong.name + skd_info.danyuan.name + skd_info.fangwu.name }}</span>
                        <span :class="skd_info.status == 1 ? 'text-decoration-line-through' : ''">{{
                        skd_info._id
                        }}</span>
                        <span>{{ skd_info.fangwu.yezhu }}</span>
                    </div>
                    <!-- content -->
                    <v-simple-table style="background:none;border:thin solid rgba(0, 0, 0, 0.38)" dense>
                        <template v-slot:default>
                            <thead>
                                <tr>
                                    <th class="text-center"></th>
                                    <th class="text-center"></th>
                                    <th class="text-center"></th>
                                    <th class="text-center"></th>
                                    <th class="text-center">/</th>
                                    <th class="text-center"></th>
                                    <th class="text-center"></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="item in skd_info.feiyong" :key="item._id">
                                    <td class="text-center">{{ item.mingcheng }}</td>
                                    <td class="text-center">{{ item.danjia }}</td>
                                    <td class="text-center">{{ item.start }}</td>
                                    <td class="text-center">{{ item.end }}</td>
                                    <td class="text-center">{{ item.zhouqi_beishu }}</td>
                                    <td class="text-center">{{ item.shuliang }}</td>
                                    <td class="text-center">{{ item.zongji }}</td>
                                </tr>
                            </tbody>
                        </template>
                    </v-simple-table>
                    <!-- footer -->
                    <div>
                        <div class="d-flex justify-space-between text-button font-weight-black">
                            <span>{{ yingshou }}{{ youhui != 0 ? ' - ' +
                            skd_info.youhui : ''
                            }} =
                                {{ shishou }}</span>
                            <span>{{ convertCurrency(shishou) }}</span>
                        </div>
                        <span>{{ skd_info.remark }}</span>
                        <span class="float-right">{{ skd_info.user?skd_info.user.nickname:'' }} @
                            {{ skd_info.pay_way }} @ {{ skd_info.date_china }}</span>
                    </div>
                </div>
                <!--  -->
            </div>
        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="$emit('close')">
                 
            </v-btn>
            <v-btn text>
                 
            </v-btn>
        </v-card-actions>
    </v-card>
</template>
<script>
export default {
    data: () => ({
        skd_info: {
            xiaoqu: {},
            loudong: {},
            danyuan: {},
            fangwu: {},
            user: {},
        },
        loading: false,
        yingshou: 0.00,
        youhui: 0.00,
        shishou: 0.00,
        cancel_remark: '',
        valid: false
    }),
    props: {
        shoukuandan_id: String
    },
    watch: {
        shoukuandan_id(val) {
            this.skd_info = {
                xiaoqu: {},
                loudong: {},
                danyuan: {},
                fangwu: {},
                user: {},
            }
            this.yingshou = 0.00
            this.youhui = 0.00
            this.shishou = 0.00
            this.cancel_remark = ''
            // 
            if (val != '') {
                this.get_shoukuandan(val)
            }
        }
    },
    created() {
        // 
        this.get_shoukuandan(this.shoukuandan_id)
    },
    methods: {
        get_shoukuandan(_id) {
            this.loading = true
            this.$axios.get('shoukuandan', { params: { _id: _id } })
                .then((res) => {
                    setTimeout(() => {
                        this.skd_info = res.data
                        this.yingshou = Number(res.data.total).toFixed(2)
                        this.youhui = Number(res.data.youhui).toFixed(2)
                        this.shishou = (this.yingshou - this.youhui).toFixed(2)
                        this.loading = false
                    }, 500);
                })
        },

        // 
        btn_cancel() {

            this.$axios.post('/shoukuandan/cancel', { shoukuandan: this.skd_info, remark: this.cancel_remark })
                .then((res) => {
                    if (res.data.message == '') {
                        this.get_shoukuandan(this.shoukuandan_id)
                    }
                })
        },

        // 
        convertCurrency(n) {
            var fraction = ['', ''];
            var digit = [
                '', '', '', '', '',
                '', '', '', '', ''
            ];
            var unit = [
                ['', '', ''],
                ['', '', '', '']
            ];
            var head = n < 0 ? '' : '';
            n = Math.abs(n);
            var s = '';
            for (let i = 0; i < fraction.length; i++) {
                s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '');
            }
            s = s || '';
            n = Math.floor(n);
            for (let i = 0; i < unit[0].length && n > 0; i++) {
                var p = '';
                for (let j = 0; j < unit[1].length && n > 0; j++) {
                    p = digit[n % 10] + unit[1][j] + p;
                    n = Math.floor(n / 10);
                }
                s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s;
            }
            return head + s.replace(/(.)*/, '')
                .replace(/(.)+/g, '')
                .replace(/^$/, '');
        }
    }
}
</script>